* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.App {
    height: 100vh;
    width: 100%;
    background: rgb(174, 238, 191);
    background: radial-gradient(circle, rgba(174, 238, 191, 1) 0%, rgba(148, 183, 233, 1) 100%);
    position: relative;
}

.Title {
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%, -10%);
    -webkit-transform: translate(-50%, -10%);
    -ms-transform: translate(-50%, -10%);
}


.Title h1 {
    font-size: 40px;
    letter-spacing: 7px;
    color: rgb(58, 58, 58)
}

.hidden {
    z-index: -1;
    opacity: 0;
    transition: all 2s;
    cursor: none;
}

.Infos {
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -40%);
    -webkit-transform: translate(-50%, -40%);
    -ms-transform: translate(-50%, -40%);
    transition: all 2s;
}

.Infos .start {
    color: rgb(239, 234, 234);
    border-top: 4px solid #48ecc4;
    border-right: 4px solid #0a5f4a;
    border-bottom: 4px solid #0f745b;
    border-left: 4px solid #8cf9de;
    border-radius: 0;
    background: lightseagreen;
    padding: 12px 25px;
    font-size: 21px;
    letter-spacing: 4px;
    transition: all 0.5s;
    cursor: pointer;
}

.Infos .start:hover {
    border-top: 4px solid #0f745b;
    border-right: 4px solid #8cf9de;
    border-bottom: 4px solid #48ecc4;
    border-left: 4px solid #0a5f4a;
}

.Infos .start:active {
    background: rgb(17, 133, 127);
}

.Games {
    transition: all 2s;
    position: absolute;
    left: 50%;
    top: 63%;
    transform: translate(-50%, -63%);
    -webkit-transform: translate(-50%, -63%);
    -ms-transform: translate(-50%, -63%);
    display: flex;
    flex-direction: column;
}

.Games .end {
    letter-spacing: 2px;
    width: 100px;
    margin: 0 0 0 auto;
    color: rgb(239, 234, 234);
    border-top: 4px solid #48ecc4;
    border-right: 4px solid #0a5f4a;
    border-bottom: 4px solid #0f745b;
    border-left: 4px solid #8cf9de;
    border-radius: 0;
    background: lightseagreen;
    padding: 5px 10px;
    font-size: 12px;
    transition: all 0.5s;
    cursor: pointer;
}

.Games .end:hover {
    border-top: 4px solid #0f745b;
    border-right: 4px solid #8cf9de;
    border-bottom: 4px solid #48ecc4;
    border-left: 4px solid #0a5f4a;
}

.Games .end:active {
    background: rgb(17, 133, 127);
}

.Games .content {
    position: relative;
    height: 365px;
    width: 527px;

    background-image: linear-gradient(0deg, transparent calc(100% - 1px), #f0f0f0 calc(100% - 1px)),
        linear-gradient(90deg, transparent calc(100% - 1px), #f0f0f0 calc(100% - 1px));
    background-size: 16px 16px;
    background-repeat: repeat;
    background-position: center center;
    border: 1px solid white;

    padding: 10px;
}

.Games .question p {
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 2px;
    color: rgb(58, 58, 58);
    border: 2px solid rgb(58, 58, 58);
    border-radius: 10px;
    padding: 5px 10px;
    height: 75px;
    display: flex;
    align-items: center;
}

.Games .hint p {
    letter-spacing: 3px;
    font-size: 20px;
    letter-spacing: 2px;
    color: rgb(58, 58, 58);
    text-align: center;
    padding: 15px
}

.Games .answer p {

    letter-spacing: 3px;
    font-size: 20px;
    letter-spacing: 2px;
    color: rgb(58, 58, 58);
    text-align: center;
    padding: 15px
}

.Games .next {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -0%);
    -webkit-transform: translate(-50%, -0%);
    -ms-transform: translate(-50%, -0%);
}

.Games .next button {
    letter-spacing: 2px;
    width: 100px;
    margin: 0 0 0 auto;
    color: rgb(239, 234, 234);
    border-top: 4px solid #48ecc4;
    border-right: 4px solid #0a5f4a;
    border-bottom: 4px solid #0f745b;
    border-left: 4px solid #8cf9de;
    border-radius: 0;
    background: lightseagreen;
    padding: 5px 10px;
    font-size: 15px;
    transition: all 0.5s;
    cursor: pointer;
}

.Games .next button:hover {
    border-top: 4px solid #0f745b;
    border-right: 4px solid #8cf9de;
    border-bottom: 4px solid #48ecc4;
    border-left: 4px solid #0a5f4a;
}


.Games .next button:active {
    background: rgb(17, 133, 127);
}


.Games .hint {
    margin: 20px 0 0 0;
}

.Games .hint button {
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 240px;
    padding: 10px 25px;
    color: #100f0f;
    transition: 0.3s ease-in-out;
    font-weight: 600;
    background: #c4f5b1;
    overflow: hidden;
    border-radius: 10px;
    width: 400px;
}

.Games .hint button:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 150%;
    height: 500%;
    content: "";
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    -webkit-transform: translateX(-98%) translateY(-25%) rotate(45deg);
    transform: translateX(-98%) translateY(-25%) rotate(45deg);
    background: #68ea39;
    opacity: 0.2;
}

.Games .hint button:hover:before {
    -webkit-transform: translateX(-9%) translateY(-25%) rotate(45deg);
    transform: translateX(-9%) translateY(-25%) rotate(45deg);
}

.Games .content .hidden {
    height: 0;
    visibility: hidden;
    transition: all 0s;
}

.Games .answer button {
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 240px;
    padding: 10px 25px;
    color: #100f0f;
    transition: 0.3s ease-in-out;
    font-weight: 600;
    background: #f5c3b1;
    overflow: hidden;
    border-radius: 10px;
    width: 400px;
}

.Games .answer button:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 150%;
    height: 500%;
    content: "";
    -webkit-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    -webkit-transform: translateX(-98%) translateY(-25%) rotate(45deg);
    transform: translateX(-98%) translateY(-25%) rotate(45deg);
    background: #ea5439;
    opacity: 0.2;
}

.Games .answer button:hover:before {
    -webkit-transform: translateX(-9%) translateY(-25%) rotate(45deg);
    transform: translateX(-9%) translateY(-25%) rotate(45deg);
}